@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,300,700);
:root {
  --white: #f8f9fa;
  --light-grey: #dee2e6;
  --grey: #adb5bd;
  --dark-grey: #495057;
  --black: #212529;
}

body {
  direction: rtl;
  /* background-color: var(--blue); */
}

* {
  font-family: 'BYekan';
}

.form {
  width: 100% !important;
  height: 100vh !important;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background: #dee2e6;
  background: var(--light-grey);
  box-shadow: 7px 7px 14px #dadada, -7px -7px 14px #ffffff;
}

.form-container {
  width: 90%;
  padding: 2rem 0.5rem;
  border-radius: 10px;
  background: #dee2e6;
  background: var(--light-grey);
  box-shadow: 9px 9px 31px #a8a8a8, -9px -9px 31px #e4e4e4;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  margin-top: 2rem;
}

.header {
  text-align: center;
  color: #212529;
  color: var(--black);
  font-size: 1.35rem;
  font-family: 'BYekan';
  padding-bottom: 34px;
  position: relative;
}

.header::before {
  content: '';
  position: absolute;
  width: 222px;
  height: 1px;
  bottom: 11px;
  right: 50%;
  transform: translate(50%, -50%);
  background-color: #212529;
  background-color: var(--black);
  -webkit-transform: translate(50%, -50%);
  -moz-transform: translate(50%, -50%);
  -ms-transform: translate(50%, -50%);
  -o-transform: translate(50%, -50%);
}

.input-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  padding: 15px 0;
}
.input-container input {
  width: 90%;
  padding: 5px 0;
  border: none;
  border-bottom: 2px solid #212529;
  border-bottom: 2px solid var(--black);
  /* color: var(--light-blue); */
  outline: none;
  margin: auto;
  background-color: transparent;
}
input::-webkit-input-placeholder {
  font-family: 'BYekan';
  color: #212529;
  color: var(--black);
}
input:-ms-input-placeholder {
  font-family: 'BYekan';
  color: #212529;
  color: var(--black);
}
input::placeholder {
  font-family: 'BYekan';
  color: #212529;
  color: var(--black);
}
.input-container label {
  padding: 10px 0;
  text-align: center;
  background-color: transparent;
}
.input-container span {
  position: absolute;
  right: 8px;
  top: 45px;
  font-size: 19px;
}

.submit {
  margin: 10px auto;
  display: block;
  width: 105px;
  border: none;
  color: #f8f9fa;
  color: var(--white);
  background-color: var(--dark-blue);
  padding: 7px 15px;
  font-family: 'BYekan';
  font-size: 16px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  cursor: pointer;
}

.error {
  color: rgb(184, 61, 61);
  margin-right: 17px;
  margin-top: 4px;
  margin-left: auto;
}

.btns {
  display: flex;
  flex-direction: column;
}

.register {
  color: var(--dark-blue);
  display: block;
  margin: 1rem auto;
}
.register:hover {
  text-decoration: none;
  color: var(--dark-blue);
}

.get-code-text {
  text-align: center;
}

.input-code {
  width: 60%;
  display: block;
  margin: auto;
  border: 2px solid rgb(172, 172, 172);
  border-radius: 5px;
  margin: 2rem auto;
  padding: 5px 0;
  outline: none;
}
.resend {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
.resend-code-btn {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
}
.resend-code-btn span {
  color: green;
  margin-top: 3px;
  margin-right: 5px;
  font-size: 16px;
}
.resend-text {
  border: none;
  background: transparent;
  color: green;
  font-family: 'BYekan';
}

.resend-code-text {
  margin: 0;
}

.forget-code {
  color: black;
  /* margin-left: auto */
  text-align: center;
}
.forget-code:hover {
  text-decoration: none;
  color: black;
}

.input-container select {
  width: 90%;
  font-family: 'BYekan';
  margin: auto;
  padding: 10px;
  border: none;
  border-bottom: 2px solid rgb(155, 155, 155);
  outline: none;
  background-color: transparent;
}

.change-password {
  padding: 10px 0;
}

#grade {
  width: 91%;
  margin: auto;
  border: none;
}

/* ===== Responsive ===== */

@media screen and (min-width: 425px) {
  .form-container {
    width: 75%;
  }
}
@media screen and (min-width: 720px) {
  .form-container {
    width: 60%;
  }
  .error {
    margin-right: 23px;
  }
}
@media screen and (min-width: 900px) {
  .form-container {
    width: 37%;
  }
}

:root {
  --white: #f6f6f6;
  --light-grey: #c6c6c6;
  --grey: #949494;
  --dark-grey: #1f1f1f;
  --black: #000000;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'vazir';
}
header {
  width: 100%;
  height: auto;
}
nav {
  width: 100%;
  height: auto;
  background-color: #949494;
  background-color: var(--grey);
  display: flex;
  align-items: center;
  flex-direction: column;
  z-index: 100;
  position: fixed;
  top: 0;
}
.fixed-nav {
  background-color: #f6f6f6;
  background-color: var(--white);
}
.top-nav {
  padding: 5px;
  /* background-color: var(--grey); */
  box-shadow: 1px 1px 4px #cccc, 1px 0px 4px #d8d8d8;
  background-color: #fefefe;
  display: flex;
  align-items: center;
  width: 100%;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  justify-content: space-between;
  flex-direction: row;
}

.left-flex {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 25%;
}

.logo {
  width: 50px;
  height: 50px;
  overflow: hidden;
  margin-left: 40px;
}
.logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
}

.login-button {
  width: 125px;
  background: #bdbdbd;
  /* box-shadow: 20px 20px 60px #a8a8a8, -20px -20px 60px #e4e4e4; */
  padding: 8px 0;
  margin-right: 47px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #1f1f1f;
  color: var(--dark-grey);
  border-radius: 10px;
  transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -ms-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.username {
  cursor: pointer;
}

.username a {
  color: #1f1f1f;
  color: var(--dark-grey);
  font-size: 18px;
}

.username a:hover {
  color: #000000;
  color: var(--black);
}

.login-button:hover {
  border-radius: 35px;
  -webkit-border-radius: 35px;
  -moz-border-radius: 35px;
  -ms-border-radius: 35px;
  -o-border-radius: 35px;
}

.login-button a {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #000000;
  color: var(--black);
}

.icons {
  padding-left: 10px;
}
.icons i {
  padding: 0 10px;
  font-size: 20px;
  color: black;
}

.menu {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  width: 65%;
  list-style: none;
}

.menu .menu-item {
  background-color: transparent;
  padding: 5px 2vw;
  border-radius: 7px;
  -webkit-border-radius: 7px;
  -moz-border-radius: 7px;
  -ms-border-radius: 7px;
  -o-border-radius: 7px;
  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  color: #1f1f1f;
  color: var(--dark-grey);
}
.active-item {
  border-radius: 10px 0 10px 0;
  -webkit-border-radius: 10px 0 10px 0;
  -moz-border-radius: 10px 0 10px 0;
  -ms-border-radius: 10px 0 10px 0;
  -o-border-radius: 10px 0 10px 0;
  background-color: var(--orange);
  padding: 5px 2vw;
}
.menu .menu-item:hover {
  background-color: #ea8e13;
}

.menu-item:hover .menu-link {
  color: #f6f6f6;
  color: var(--white);
}

.menu .menu-link {
  text-decoration: none;
  color: #000000;
  color: var(--black);

  transition: all 0.15s linear;
  -webkit-transition: all 0.15s linear;
  -moz-transition: all 0.15s linear;
  -ms-transition: all 0.15s linear;
  -o-transition: all 0.15s linear;
}
.mobile-menu {
  display: none;
}
.close-btn {
  display: none;
}

/* ======== responsiv ======== */

@media screen and (max-width: 1200px) {
  .left-flex {
    width: 35%;
  }
  .logo {
    margin-left: 0;
  }
}

@media screen and (max-width: 800px) {
  .left-flex {
    width: 50%;
  }
}
@media screen and (max-width: 750px) {
  nav {
    height: 45px;
  }
  .top-nav {
    width: 100%;
    position: absolute;
    right: -100%;
    height: 100vh;
    flex-direction: column;
    background-color: #c6c6c6;
    background-color: var(--light-grey);
  }
  .left-flex {
    justify-content: flex-start;
    flex-direction: row-reverse;
    width: 100%;
    margin-left: 25px;
    margin-right: 10px;
  }
  .logo {
    position: relative;
  }
  .menu {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: -9rem;
  }
  .menu .menu-item {
    margin: 1.4rem 0;
    padding: 5px 10vw;
  }
  .mobile-menu {
    display: block;
    position: absolute;
    right: 10px;
    top: 10px;
    width: 27px;
    height: 21px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    z-index: 0;
  }

  .login {
    position: absolute;
    top: 16%;
    left: 37%;
  }

  .close-btn {
    position: absolute;
    display: block;
    left: 10px;
    top: 10px;
    width: 30px;
    height: 30px;
    background-color: transparent;
    border: none;
  }
  .close-btn .bar {
    width: 100%;
    height: 5px;
    background-color: #f6f6f6;
    background-color: var(--white);
    position: absolute;
    right: 0;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    z-index: 10;
    transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
  }
  .close-btn .bar:nth-child(1) {
    transform: rotate(45deg) translate(6px, 6px);
    -webkit-transform: rotate(45deg) translate(6px, 6px);
    -moz-transform: rotate(45deg) translate(6px, 6px);
    -ms-transform: rotate(45deg) translate(6px, 6px);
    -o-transform: rotate(45deg) translate(6px, 6px);
  }
  .close-btn .bar:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
    -webkit-transform: rotate(-45deg) translate(5px, -5px);
    -moz-transform: rotate(-45deg) translate(5px, -5px);
    -ms-transform: rotate(-45deg) translate(5px, -5px);
    -o-transform: rotate(-45deg) translate(5px, -5px);
  }
  .close-btn .bar:nth-child(2) {
    opacity: 0;
  }
  .show-menu {
    right: 0;
    transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
  }
  .mobile-menu .bar {
    width: 100%;
    height: 5px;
    background-color: #1f1f1f;
    background-color: var(--dark-grey);
    position: absolute;
    right: 0;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    z-index: 10;
    transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
  }
  .bar:nth-child(1) {
    top: 0;
  }
  .bar:nth-child(2) {
    top: 8px;
  }
  .bar:nth-child(3) {
    top: 16px;
  }
}

:root {
  --white: #f8f9fa;
  --light-grey: #c6c6c6;
  --grey: #adb5bd;
  --dark-grey: #363636;
  --black: #212529;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* 
body {
  background-color: var(--light-blue);
} */

.courses {
  padding: 2rem;
  margin-top: 5rem;
}

.courses-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 100px;
  gap: 100px;
  width: auto;
  margin: auto;
  padding: 3rem 1rem;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  justify-items: center;
  border-radius: 8px;
  background: #c6c6c6;
  background: var(--light-grey);
  box-shadow: 13px 13px 26px #2e2e2e, -13px -13px 26px #3e3e3e;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  border: 10px solid #363636;
  border: 10px solid var(--dark-grey);
  overflow: hidden;
}

.courses-container .item {
  padding: 1rem 1rem 1.5rem;
  background-color: #c6c6c6;
  background-color: var(--light-grey);
  /* box-shadow: 13px 13px 19px #bcbcbc, -13px -13px 19px #d0d0d0; */
  /* background: linear-gradient(145deg, #b2b2b2, #d4d4d4); */
  box-shadow: 8px 8px 16px #a4a4a4, -8px -8px 16px #e8e8e8;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.image img {
  cursor: pointer;
  width: 100%;
  object-fit: cover;
}
.name {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1em;
  cursor: pointer;
}

/* ====== responsiv ======= */
@media screen and (min-width: 650px) {
  .courses-container {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (min-width: 900px) {
  .courses-container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (min-width: 1300px) {
  .courses-container {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

