.telegram p,
.instagram p,
.web-site p {
  font-family: cursive;
}
.telegram {
  border: 2px solid #0077ff;
}
.instagram {
  border: 2px solid #ff0066;
}
.phone {
  border: 2px solid #009933;
}
.web-site {
  border: 2px solid #767e7e;
  border: 2px solid var(--grey);
}
.gmail i {
  color: #ea4335;
}
.telegram i {
  color: #0077ff;
}
.instagram i {
  color: #ff0066;
}
.phone i {
  color: #009933;
}
.web-site i {
  color: #767e7e;
  color: var(--grey);
}
