  background-color: #ea4335;
  box-shadow: 10px 10px 20px rgb(201, 201, 201);
  border: none;
}
.telegram:hover {
  background-color: #0077ff;
  box-shadow: 10px 10px 20px rgb(201, 201, 201);
  border: none;
}
