  /* background-color: #ff0066; */
  background: rgb(245, 133, 41);
  background: linear-gradient(
    17deg,
    rgba(245, 133, 41, 1) 14%,
    rgba(221, 42, 123, 1) 53%,
    rgba(81, 91, 212, 1) 94%
  );
  box-shadow: 10px 10px 20px rgb(201, 201, 201);
  border: none;
}
.phone:hover {
  background-color: #009933;
  box-shadow: 10px 10px 20px rgb(201, 201, 201);
  border: none;
}
.web-site:hover {
  background-color: #767e7e;
  background-color: var(--grey);
  box-shadow: 10px 10px 20px rgb(201, 201, 201);
  border: none;
}
.contact i {
  font-size: 50px;
  -webkit-transform: translate(0, 20px);
          transform: translate(0, 20px);
  transition: all 0.5s ease-in-out;
}
.contact:hover i {
  -webkit-transform: translate(0, -20px);
          transform: translate(0, -20px);
}
.contact:hover {
  -webkit-transform: translateY(-6px);
          transform: translateY(-6px);
  cursor: pointer;
}
.contact:hover i,
.contact:hover p,
.contact:hover a {
  color: #fefefe;
  color: var(--white);
}

.contact:hover p,
.contact:hover a {
  opacity: 1;
}
.contact p {
  font-size: 18px;
}
.text {
  opacity: 0;
  color: #fefefe;
  color: var(--white);
  transition: all 0.5s ease-in-out;
}

.contact .fa-phone {
  transform: translate(0, 30px) !important;
  -webkit-transform: translate(0, 30px) !important;
  -moz-transform: translate(0, 30px) !important;
  -ms-transform: translate(0, 30px) !important;
  -o-transform: translate(0, 30px) !important;
}

.contact:hover .fa-phone {
  transform: translate(0, -10px) !important;
  -webkit-transform: translate(0, -10px) !important;
  -moz-transform: translate(0, -10px) !important;
  -ms-transform: translate(0, -10px) !important;
  -o-transform: translate(0, -10px) !important;
}

