:root {
  --white: #f6f6f6;
  --light-grey: #c6c6c6;
  --grey: #949494;
  --dark-grey: #363636;
  --black: #000000;
}

.panel-contain {
  width: 100%;
  position: relative;
  direction: rtl;
}

.fa-bars {
  color: #f6f6f6;
  color: var(--white);
}

.panel-nav {
  width: 60px;
  height: 100%;
  position: fixed;
  background-color: rgb(31, 35, 39);
  border-right: 20px solid rgb(31, 35, 39);
  transition: 0.5s;
  overflow: hidden;
  z-index: 10;
}

.panel-nav.show {
  width: 300px;
}

.panel-nav.active {
  width: 90px;
}

.panel-nav ul {
  position: absolute;
  top: 10%;
  left: 0;
  width: 100%;
  margin-top: 30px;
}

.panel-nav .fa-bars {
  font-size: 23px;
  position: absolute;
  right: 0;
}
.panel-nav ul li {
  position: relative;
  list-style: none;
  transition: 0.5s;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  cursor: pointer;
}

/* .panel-nav ul li:hover,
.panel-nav ul li.hovered {
  background-color: #fff;
} */

.panel-nav ul li a {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #fff;
  cursor: default;
}

.panel-nav ul li:nth-child(5) {
  margin-top: 16rem;
}
i {
  cursor: pointer;
}

.panel-nav ul li a span {
  margin-right: 20px;
}

/* .panel-nav ul li:nth-child(1) {
  margin-bottom: 10rem;
  pointer-events: none;
} */

/* .panel-nav ul li:hover a,
.panel-nav ul li.hovered a {
  color: rgb(31, 35, 39);
} */

.panel-nav ul li a .icon {
  display: block;
  position: relative;
  min-width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 1.75em;
}

.panel-nav ul li a .panel-title {
  display: block;
  position: relative;
  padding: 0 10px;
  height: 60px;
  line-height: 60px;
}
/* panel-panel-main  */

.panel-main {
  /* position: absolute; */
  width: 100%;
  /* right: 300px; */
  height: 100vh;
  transition: 0.5s;
  background-color: #c6c6c6;
  background-color: var(--light-grey);
  padding-right: 60px;
}

.panel-main.active {
  width: calc(100% - 90px);
  right: 90px;
}

.panel-bar {
  height: 10%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-left: 35px;
}

.Hmenu {
  position: relative;
  top: 0;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  cursor: pointer;
}

.panel-user {
  position: relative;
  /* top: 18px; */
  width: 95px;
  overflow: hidden;
  cursor: pointer;
}

.panel-user img {
  position: absolute;
  top: 0px;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.panel-table {
  width: 100%;
  /* height: 100vh; */
}
/* box */

.card {
  position: relative;
  width: 100%;
  /* padding: 20px; */
  display: flex;
  justify-content: space-between;
}

.box {
  position: relative;
  width: 28%;
  margin: 30px 30px;
  height: 100px;
  overflow: hidden;
}

.card .boxs {
  position: relative;
  background: rgba(205, 208, 209, 0.733);
  width: 100%;
  height: 100%;
  /* box-shadow: 0 7px 15px rgba(211, 208, 208, 0.856); */
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
  transition: 0.3s;
}

.number {
  position: relative;
  font-size: 2.5em;
  color: rgb(31, 35, 39);
}

.cardname {
  color: rgba(88, 97, 97, 0.945);
  font-size: 1.5em;
  margin-top: 5px;
}

.boxs i {
  font-size: 2em;
  color: rgb(124, 123, 121);
}

.boxs:hover {
  background-color: rgb(31, 35, 39);
}

.boxs:hover .number,
.boxs:hover .cardname,
.boxs:hover i {
  color: aliceblue;
}
.panel-courses {
  width: 100%;
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* change password */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.pas-conainer {
  width: 100%;
  height: 90vh;
  /* background: url('./pass.jpg');
  background-repeat: no-repeat;
  background-size: contain; */
  background-color: #373a36;
  display: flex;
  justify-content: center;
  align-items: center;
}
.forgot-password {
  width: 30rem;
  height: 20rem;
  display: flex;
  flex-direction: column;
  background-color: #a79e9ef3;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  justify-content: center;
  align-items: center;
}
.forgot-password h1 {
  position: relative;
  top: -35px;
  text-align: center;
  font-size: 30px;
  color: rgba(230, 234, 238, 0.959);
}
.password-input {
  margin: 5px 0;
  padding: 7px 25px;
  border-radius: 10px;
  border: none;
  outline: none;
  text-align: right;
  font-size: 15px;
  transition: 0.3s;
  background-color: #373a36;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.change-pas-btn {
  position: relative;
  padding: 5px 30px;
  border: none;
  transition: 0.3s;
  cursor: pointer;
  border-radius: 5px;
  font-size: 17px;
  top: 15px;
  background-color: #e6e2dd;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.password-input:hover,
.password-input.hovered {
  background-color: #e6e2dd;
}
.change-pas-btn:hover {
  background-color: rgba(85, 79, 79, 0.596);
  color: #e6e2dd;
}

.page-not-found-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #e5e5e5;
}

.not-found-img {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.not-found-img img {
  width: 50%;
}
.not-found-text {
  font-size: 25px;
  text-align: center;
  font-weight: 100;
  color: #747474;
}

@media screen and (max-width: 600px) {
  .not-found-text {
    font-size: 20px;
    margin-top: 30px;
  }
  .not-found-img img {
    width: 60%;
  }
}
@media screen and (max-width: 320px) {
  .not-found-text {
    font-size: 17px;
  }
}

body {
  background-color: #262626;
}
.main-one-part-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 6rem;
}
.title-header {
  margin-top: 20px;
  background-color: #bbb;
  width: 85%;
  text-align: center;
  border-radius: 20px;
  padding: 20px 0;
  font-size: 20px;
}
.show-one-part {
  margin-top: 70px;
  display: flex;
  align-items: center;
  background-color: #bbb;
  justify-content: space-around;
  width: 75%;
  border-radius: 10px;
  margin-bottom: 30px;
}

.show-one-part img {
  width: 38%;
}
.show-one-part span {
  width: 62%;
  text-align: center;
  font-size: 20px;
}

.part {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.part h2 {
  background-color: #fff;
  border-radius: 30% 70% 30% 70% / 100% 0% 100% 0%;
  margin: 20px;
  margin-left: auto;
  padding: 20px;
  font-size: 15px;
  text-align: center;
  width: 25%;
}

.part .ti-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.btn {
  width: 100px;
  height: 40px;
  margin-left: 20px;
  border-radius: 10px;
  outline: none;
  border: none;
}
.btn:hover {
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #fff;

  color: #fff;
  transition: all 0.3s ease-in-out;
}
.part .border {
  width: 90%;
  height: 5px;
  border-radius: 2px;
  background-color: #bbb;
}
video {
  width: 80%;
  border-radius: 10px;
  object-fit: contain;
}
.courses-video-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.video-container {
  padding-top: 20px;
  width: 30%;
  background-color: #bbb;
  margin: 25px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.video-container h3 {
  margin-bottom: 0;
}

.part h3 {
  margin: 14px 0;
}
.part h4 {
  margin: 14px 0;
}
@media screen and (max-width: 990px) {
  .video-container {
    margin: 15px;
  }
  .show-one-part span {
    font-size: 17px;
    margin-right: 10px;
  }
  .show-one-part img {
    width: 270px;
    height: 270px;
  }
  .part .ti-btn h2 {
    width: 25%;
    font-size: 13px;
  }
  .part h3 {
    font-size: 18px;
  }
  .part h4 {
    font-size: 15px;
  }
}
@media screen and (max-width: 700px) {
  .title-header {
    width: 90%;
  }
  .part h2 {
    width: 35% !important;
    font-size: 13px !important;
  }
  .btn {
    width: 20%;
    font-size: 12px;
  }
  .courses-video-container {
    flex-direction: column;
  }
  .video-container {
    width: 65%;
  }
  .video-container video {
    margin-top: 20px;
  }
  .part h4 {
    margin-bottom: 2rem;
  }
  .show-one-part {
    flex-direction: column-reverse;
    width: 80%;
  }
  .show-one-part span {
    margin-bottom: 20px;
    padding: 10px;
    width: 100%;
  }
}
@media screen and (max-width: 400px) {
  .show-one-part img {
    width: 90%;
    height: 60%;
  }
  .show-one-part span {
    margin-bottom: 20px;
    padding: 10px;
    width: 100%;
  }
  .title-header h1 {
    font-size: 17px !important;
  }
  .ti-btn {
    width: 75%;
    justify-content: flex-start !important;
    flex-direction: column;
  }
  .ti-btn h2 {
    margin: 20px auto;
    border-radius: 15px;
    font-size: 12px !important;
    width: 62% !important;
  }
  .ti-btn h2:hover {
    border-radius: 15px;
    background-color: #c04848;
    transition: all 0.3s ease-in-out;
    color: #fff;
  }
  .ti-btn .btn {
    margin: auto;
    width: 30%;
  }
  .video-container {
    width: 85%;
    padding: 0;
  }
}

:root {
  --white: #f8f9fa;
  --light-grey: #c6c6c6;
  --grey: #adb5bd;
  --dark-grey: #363636;
  --black: #212529;
}

.main-val-container {
  width: 100%;
  background-color: #262626;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 5rem;
}
.header-val-content {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90%;
  height: 100px;
  background-color: #bbb;
  color: #fff;
  text-align: center;
  margin-top: 20px;
  border-radius: 10px;
}
.val-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.row {
  display: flex;
  align-items: center;
}
.row-1 {
  margin-top: 50px;
  margin-bottom: 50px;
  width: 70%;
  background-color: #bbb;
  flex-direction: row-reverse;
  justify-content: space-around;
  border-radius: 10px;
}
.row-1 span {
  font-size: 20px;
  padding: 20px;
  text-align: center;
}
.row-2 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
}
.img img {
  width: 100%;
  padding: 20px;
}

.valuation {
  border-radius: 10px;
  background-color: #bbb;
  width: 40%;
  padding: 30px;
  margin: 20px;
  text-align: center;
}
.valuation h2 {
  margin-bottom: 20px;
}

@media screen and (max-width: 900px) {
  .row-1,
  .row-2 {
    flex-direction: column;
    align-items: center;
  }
  .row-1 {
    width: 60%;
  }
  .valuation {
    width: 50%;
  }
  .img {
    width: 70%;
  }
  .row-1 img {
    width: 100%;
    padding: 20px;
  }
  .row-1 span {
    font-size: 20px;
    padding: 20px;
    text-align: center;
  }
}
@media screen and (max-width: 700px) {
  .row-1 {
    flex-direction: column;
    width: 70%;
  }
  .img {
    width: 80%;
  }
  .row-1 img {
    width: 100%;
    padding: 20px;
  }
  .row-1 span {
    margin-bottom: 20px;
  }
  .row-2 {
    flex-direction: column;
  }
  .valuation {
    width: 70%;
    margin-bottom: 30px;
  }
  .valuation h2 {
    font-size: 20px;
  }
  .valuation span {
    font-size: 18px;
  }
}
@media screen and (max-width: 450px) {
  .header-val-content h1 {
    font-size: 25px;
  }
  .img {
    width: 90%;
  }
  .row-1 {
    width: 90%;
  }
  .row-1 img {
    width: 100%;
    padding: 20px;
  }
  .row-1 span {
    font-size: 15px;
  }
  .valuation {
    width: 85%;
    padding: 10px;
    margin: 15px;
  }
  .valuation h2 {
    font-size: 18px;
    margin: 10px;
  }
  .valuation p {
    font-size: 15px;
  }
}

